import React from "react";
import "../Styles/Card.css";


export default function Card({name, id, flag, continent, extension}) {
    return (
        <div className="cardComp">
            <div className="cardComp2">
                <div className="flag">
                    <img src= {flag}
                    alt= "Flag not found"
                    width="320px"
                />
                <div className="rightCard">
                    <h1>{name}</h1>
                    <div className="rightCardBottom">
                        <div className="items">
                            <p>{id}</p>
                            <p>CODE</p>
                        </div>
                        <div className="items">
                            <p>{continent}</p>
                            <p>CONTINENT</p>                            
                        </div>
                        <div className="items">
                            <p>{extension}</p>
                            <p>AREA</p>
                        </div>
                    </div>
                </div>
                </div>
            </div>
        </div>
    )
}