import React from "react";
import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";
import {
    getCountries,
    getNameCountries,
    getDetail,
    getActivities,
    orderByName,
    orderByPopulation,
    filterByContinent,
    filterByActivities,
    postRecipe
} from "../Actions/index";
import Card from "./Card";
import Paginate from "./Paginate";
import SearchBar from "./SearchBar";
import "../Styles/home.css";



export default function Home(){
    const dispatch = useDispatch();
    const allCountries = useSelector((state) => state.countries);
    const activities = useSelector ((state) => state.activities);

    const [ currentPage, setCurrentPage ] = useState(1);
    const [ countriesPerPage, setCountriesPerPage ] = useState(10);
    const indexLastCountry = currentPage * countriesPerPage;
    const indexFirstCountry = indexLastCountry - countriesPerPage;
    const currentCountries = allCountries.slice(
        indexFirstCountry,
        indexLastCountry
    );

    const paginate = (pageNumber) => {
        setCurrentPage(pageNumber);
    };

    const [ orderName, setOrderName ] = useState("");
    const [ orderPopulation, setOrderPopulation ] = useState("");

    useEffect( () => {
        dispatch( getCountries())
    }, [ dispatch ]);

    useEffect( () => {
        dispatch( getActivities())
    }, [ dispatch ]);

    function handleClick(e){
        e.preventDefault();
        dispatch( getCountries() );
    }

    function handleSelectByContinent(e){
        e.preventDefault();
        dispatch(filterByContinent( e.target.value ))
    }

    function handleSelectByActivities(e){
        e.preventDefault();
        dispatch(filterByActivities( e.target.value ))
    }

    function handleSelectByName(e){
        e.preventDefault();
        dispatch(orderByName ( e.target.value ))
        setCurrentPage(1)
        setOrderName("Order" + e.target.value)
    }

    function handleSelectByPopulation(e){
        e.preventDefault();
        dispatch(orderByPopulation ( e.target.value ))
        setCurrentPage(1)
        setOrderPopulation("Order" + e.target.value)
    }
    

    return (
        <div className="home">
            <div>
                <h1 className="tittle">Countries App</h1>
            </div>

            <div>
                <button className="showAll"
                    onClick = { (e) => { handleClick(e) }}> View all
                </button>
                <Link to="/activity" className="linkCreate">
                    <button className="btnCreate">Create Activity</button>
                </Link>
                <SearchBar />
            </div> 

            <div className="select">
                <span className="span">Order by Name</span>
                <select onChange={(n) => handleSelectByName(n)}>
                <option value="default">All</option>
                <option value="asc">A-Z</option>
                <option value="desc">Z-A</option>
                </select>

                <span className="span">Order by Name</span>
                <select onChange={(n) => handleSelectByPopulation(n)}>
                <option value="default">All</option>
                <option value="asc">+ to -</option>
                <option value="desc">- to +</option>
                </select>


                <span className="span">Filter by Continent</span>
                <select onChange={(e) => handleSelectByContinent(e)}>
                <option value="All">All Countries</option>
                <option value="Europe">Europe</option>
                <option value="Oceania">Oceania</option>
                <option value="Americas">Americas</option>
                <option value="Africa">Africa</option>
                <option value="Asia">Asia</option>
                <option value="Antarctic">Antarctic</option>
                </select>

                <span className="span">filter by Activities</span>
                <select onChange = { (e) => handleSelectByActivities(e)}>
                <option value="All">All Countries</option>
                {activities.map((a) => (
                    <option value={ a.name } key={ a.id }>{a.name}</option>
                ))}
                </select>


            </div>

            <div className="paginate">
                <Paginate
                countriesPerPage = { countriesPerPage }
                allCountries = { allCountries.length }
                paginate = { paginate }
                />
            </div>

            <div className="cards">
                {currentCountries?.map((c) => (
                    <div key={c.id}>
                        <Link to = { "/home/" + c.title } className = "linkCard">
                            <Card
                            name = { c.name }
                            flag = { c.flag }
                            continent = { c.continent }
                            extension = { c.extension }
                            id = { c.id }
                            />
                        </Link>
                    </div>    
                ))}

            <span className="footer"> Made by Jorgechdea</span>

            </div>

        </div>
    )
}

